/*----------------------------------
VARIABLES
------------------------------------*/
:root {
	--alittle_black: rgb(25, 25, 25);
	--alittle_purple: rgb(144 60 255);
	--dirtywhite: rgb(223, 223, 223);
	--super_black: rgb(0, 0, 0);
	--orange: rgb(241 107 25 / 94%);
	--shadow: rgb(204, 204, 204);
	--gradient_black: rgb(0 0 0 / 95%);
}

/*----------------------------------
GLOBAL SELECTOR
------------------------------------*/
* {
	font-family: "Red Hat Display", sans-serif;
	font-size: 18px;
	color: var(--super_black);
	font-weight: 500;
}

h1,
h2,
h3,
h4,
h5,
h6,
span,
strong,
div,
a,
p,
ul,
li,
section,
article,
aside,
figure,
figcaption,
main,
img {
	margin-block-start: 0;
	margin-block-end: 0;
	margin-inline-start: 0;
	margin-inline-end: 0;
	transition: 800ms;
}

._no_transition {
	transition: none;
}

html,
body {
	scroll-behavior: smooth;
	color: var(--dirtywhite);
}

._body_container_inner_bg {
	background-image: url("https://images.unsplash.com/photo-1633432695903-9ef31eec6a05?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1932&q=80");
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center center;
	background-attachment: scroll;
	position: fixed;
	top: 0;
	left: 0;
	height: 100vh;
	width: 100vw;
	z-index: -100;
	opacity: 0.15;
}
